.App {
  text-align: center;
  width: 60%;
  margin: 0 auto;
}

.rc-pagination {
  margin: 0 0 30px;
  padding: 0;
  font-size: 16px;
  text-align: center;
}

.rc-pagination ul, .rc-pagination ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

.rc-pagination::after {
  display: block;
  clear: both;
  height: 0;
  overflow: hidden;
  visibility: hidden;
  content: '';
}

.rc-pagination-total-text {
  display: inline-block;
  height: 28px;
  margin-right: 8px;
  line-height: 26px;
  vertical-align: middle;
}

.rc-pagination-item {
  display: inline-block;
  min-width: 28px;
  height: 28px;
  margin-right: 8px;
  font-family: Arial, sans-serif;
  font-weight: bold;
  line-height: 28px;
  text-align: center;
  vertical-align: middle;
  list-style: none;
  background-color: transparent;
  outline: 0;
  cursor: pointer;
  user-select: none;
}

.rc-pagination-item a {
  display: block;
  padding: 0 6px;
  color: gray;
  transition: none;
}

.rc-pagination-item a:hover {
  text-decoration: none;
}

.rc-pagination-item:focus, .rc-pagination-item:hover {
  border-color: gold;
  transition: all 0.3s;
}

.rc-pagination-item:focus a, .rc-pagination-item:hover a {
  color: gold;
}

.rc-pagination-item-active {
  font-weight: 500;
  background-color: transparent;
  border-color: gold;
}

.rc-pagination-item-active a {
  color: gold;
}

.rc-pagination-item-active:focus, .rc-pagination-item-active:hover {
  border-color: gold;
}

.rc-pagination-item-active:focus a, .rc-pagination-item-active:hover a {
  color: gold;
}

.rc-pagination-jump-prev, .rc-pagination-jump-next {
  outline: 0;
}

.rc-pagination-jump-prev button, .rc-pagination-jump-next button {
  background: transparent;
  border: none;
  cursor: pointer;
  color: #666;
}

.rc-pagination-jump-prev button:after, .rc-pagination-jump-next button:after {
  display: block;
  content: '•••';
  color: gray;
}

.rc-pagination-jump-prev:hover button:after, .rc-pagination-jump-next:hover button:after {
  display: block;
  content: '•••';
  color: gold;
  transition: all 0.3s;
}

.rc-pagination-prev, .rc-pagination-jump-prev, .rc-pagination-jump-next {
  margin-right: 8px;
}

.rc-pagination-prev, .rc-pagination-next, .rc-pagination-jump-prev, .rc-pagination-jump-next {
  display: inline-block;
  min-width: 28px;
  height: 28px;
  color: rgba(0, 0, 0, 0.85);
  font-family: Arial, sans-serif;
  line-height: 28px;
  text-align: center;
  vertical-align: middle;
  list-style: none;
  border-radius: 2px;
  cursor: pointer;
  transition: all 0.3s;
}

.rc-pagination-prev, .rc-pagination-next {
  outline: 0;
}

.rc-pagination-prev button, .rc-pagination-next button {
  color: gray;
  cursor: pointer;
  user-select: none;
}

/*.rc-pagination-prev:hover button, .rc-pagination-next:hover button {*/
/*  border-color: #40a9ff;*/
/*}*/

.rc-pagination-prev .rc-pagination-item-link, .rc-pagination-next .rc-pagination-item-link {
  padding-bottom: 6px;
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  font-size: 30px;
  text-align: center;
  background-color: transparent;
  border: none;
  border-radius: 2px;
  outline: none;
  transition: all 0.3s;
}

.rc-pagination-prev:focus .rc-pagination-item-link, .rc-pagination-prev:hover .rc-pagination-item-link, .rc-pagination-next:focus .rc-pagination-item-link, .rc-pagination-next:hover .rc-pagination-item-link {
  color: gold;
  border-color: gold;
}

.rc-pagination-prev button:after {
  content: '‹';
  display: block;
}

.rc-pagination-next button:after {
  content: '›';
  display: block;
}

.rc-pagination-disabled, .rc-pagination-disabled:hover, .rc-pagination-disabled:focus {
  cursor: not-allowed;
}

.rc-pagination-disabled .rc-pagination-item-link, .rc-pagination-disabled:hover .rc-pagination-item-link, .rc-pagination-disabled:focus .rc-pagination-item-link {
  color: gold;
  border-color: transparent;
  cursor: not-allowed;
}

.rc-pagination-slash {
  margin: 0 10px 0 5px;
}

.rc-pagination-options {
  display: inline-block;
  margin-left: 16px;
  vertical-align: middle;
}